@import "./variables";
//-----------------------------------------------------------------------------------
// Common style sheet
//-----------------------------------------------------------------------------------

// ----- フッター[layouts/Footer.jsx] -----
footer.footer {
  // テキストをボックス中央にする
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $backColor;
  width: 100%;
  height: $footerHeight;
  & > p {
    display: block;
    width: 100%;
    color: $footerFontColor;
    text-align: center; } }

// ----- Dr.NAアイコン[layout/DrIcon.jsx] -----
.icon {
  padding-top: 15px;
  & > img {
    border-radius: 50%;
    border: 1px solid $mainColor;
    width:  40px;
    height: 40px;
    padding: 3px;
    background: #eee; } }

// ----- 横幅を合わせる -----
.header_item, .from_name, .display_message {
  width: 90%;
  margin: auto; }
